.widget {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // blog list widget

    &.mkdf-blog-list-widget {
        .mkdf-outer-space {
            margin-left: 0;
            margin-right: 0;
        }

        .mkdf-item-space {
            padding-left: 0;
            padding-right: 0;

            a:after {
                display: none;
            }
        }

        .mkdf-post-title {
            color: $widget-link-color;
        }

        $columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large', 'huge');
        $columns_space_width: (0, 5, 10, 15, 20, 25, 40);

        @for $i from 0 to length($columns_space_label) {
            .mkdf-#{nth($columns_space_label,$i+1)}-space {
                $column_width: nth($columns_space_width, $i+1);
                $column_width_margin: $column_width * 2;

                .mkdf-outer-space {
                    .mkdf-item-space {
                        padding-bottom: #{$column_width_margin}px;
                        margin: 0 0 #{$column_width_margin}px;
                    }
                }
            }
        }

        .mkdf-bl-simple {
            .mkdf-post-info-date {
                @include mkdfH6();

                margin-top: $widget-list-item-padding;
            }
        }

        .mkdf-bl-minimal {
            .mkdf-post-info-date {
                a {
                    color: $widget-text-color;

                    &:hover {
                        color: $widget-link-hover-color;
                    }
                }
            }
        }

        //.mkdf-date__day,
        //.mkdf-date__month-year {
        //    display: inline-block;
        //}
        //
        //.mkdf-date__line {
        //    display: none;
        //}
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // custom font widget

    &.mkdf-custom-font-widget {
        .mkdf-custom-font-holder {
            color: $widget-text-color;
        }

        a {
            .mkdf-custom-font-holder {
                opacity: 0.3;

                @include mkdfTransition(opacity .2s ease-in-out);
            }

            &:hover {
                .mkdf-custom-font-holder {
                    opacity: 1;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // follow us widget

    &.mkdf-follow-us-widget {
        text-align: left;

        // inline shit w/o image
        .mkdf-fu__inner {
            .mkdf-fu__image {
                display: none;
            }

            .mkdf-fu__icons {
                line-height: 21px;

                .mkdf-fu__subtitle {
                    @include mkdfH6();

                    color: $widget-heading-color;
                    display: inline-block;
                    line-height: inherit;
                    padding-right: 13px;

                    @if ($source==header) {
                        font-weight: 600;
                    }
                }
            }

            .mkdf-fu__link {
                margin-right: 7px;
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        // table shit w/ image
        @if ($source==sidebar or $source==sidearea) {
            .mkdf-fu__inner {
                display: table;
                width: 100%;

                .mkdf-fu__image,
                .mkdf-fu__icons {
                    display: table-cell;
                    vertical-align: middle;
                }

                .mkdf-fu__image {
                    padding-right: 45px;
                    position: relative;
                    width: 53px;

                    @include laptop-landscape-large() {
                        padding-right: 38px;
                        width: 49px;
                    }

                    &:before {
                        content: '';
                        display: block;
                        width: 1px;
                        height: 100%;
                        position: absolute;
                        right: 22px;
                        top: 0;
                        background: $widget-list-item-border-color;
                    }

                    img {
                        display: block;
                        max-width: 100%;
                        width: 100%;
                    }
                }

                .mkdf-fu__icons {
                    padding: 12px 0;

                    .mkdf-fu__subtitle {
                        display: block;
                    }
                }
            }
        }

        @if ($source==sidebar) {
            background-color: $widget-list-item-background-color;
            border-radius: $default-border-radius;
            padding: 14px 31px;

            @include laptop-landscape-large() {
                padding: 14px 0 14px 14px;
            }
        }

        @if ($source==sidearea) {
            padding: 0 !important;
            background: transparent;
            display: inline-block;
            margin: 0 auto !important;
            width: auto;
            position: absolute;
            bottom: 39px;
            left: 50%;
            transform: translateX(-50%);

            @include laptop-landscape-mac() {
                bottom: 20px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
}