aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;
}

.wpb_widgetised_column {
    position: relative;
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    $source: sidebar;

    // widgets spacing
    $widget-bottom-margin: 71px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 14px;

    $widget-title-margin: 22px;

    // list items color
    $widget-list-item-background-color: $additional-background-color;

    // list items border
    $widget-list-item-border-color: #000000;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: dotted;

    // text
    $widget-text-color: #000000;
    $widget-heading-color: #000000;

    // links
    $widget-link-color: $default-text-color;
    $widget-bottom-border: 1px solid transparent;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $small-input-height;
    $widget-tag-cloud-height: 43px;

    font-size: 16px;

    @import 'widgets/default-wordpress-widgets';
    @import 'widgets/standalone-widgets';

    counter-reset: section;

    .mkdf-widget-title-holder {
        display: flex;
        flex-wrap: nowrap;
    }

    .mkdf-widget-bullet {
        display: flex;
        flex-wrap: nowrap;
        height: 20px;

        &__number {
            display: inline-block;
            vertical-align: top;

            &:before {
                counter-increment: section;
                content: '0' counters(section, '.') '.';
                display: inline-block;
                vertical-align: top;

                @include mkdfH5();
            }
        }

        &__line {
            border-bottom: 2px solid $widget-heading-color;
            display: inline-block;
            height: 14px;
            margin: 0 8px 0 4px;
            width: 50px;
        }
    }
}