/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    color: $widget-text-color;
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .mkdf-widget-title-holder {
        padding-bottom: $widget-list-item-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: $widget-title-margin;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            border-bottom-color: $widget-list-item-border-color;
            border-bottom-style: $widget-list-item-border-style;
            border-bottom-width: $widget-list-item-border-width;
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;
            position: relative;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }

            img {
                border-radius: 50%;
            }
        }

        .children,
        .sub-menu {
            border-top-color: $widget-list-item-border-color;
            border-top-style: $widget-list-item-border-style;
            border-top-width: $widget-list-item-border-width;
            margin-top: $widget-list-item-padding;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // categories and product categories

    &.widget_categories,
    &.widget_product_categories {
        .children {
            border: none;
            position: relative;

            &:before {
                border-top-color: $widget-list-item-border-color;
                border-top-style: $widget-list-item-border-style;
                border-top-width: $widget-list-item-border-width;
                content: '';
                display: block;
                height: 1px;
                left: 0;
                position: absolute;
                top: 0;
                width: 35px;
            }
        }

        li {
            border: none;

            &:not(:last-child):before {
                border-top-color: $widget-list-item-border-color;
                border-top-style: $widget-list-item-border-style;
                border-top-width: $widget-list-item-border-width;
                bottom: 0;
                content: '';
                display: block;
                height: 1px;
                left: 0;
                position: absolute;
                width: 35px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        tr {
            border-color: $widget-list-item-border-color;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: invert($widget-list-item-background-color, 100);
            //color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {
        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .mkdf-searchform {
        .input-holder {
            background-color: $widget-list-item-background-color;
            border: none;
            border-radius: $default-border-radius;
            position: relative;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: $widget-search-form-height;
                outline: 0;
                position: relative;
                transition: color .2s ease-in-out;
                vertical-align: top;
                -webkit-appearance: none;
            }

            input.search-field {
                padding: 0 0 0 19px;
                width: calc(100% - #{$widget-search-form-height} - 5px);

                @include mkdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background-color: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 32px;
                transition: color .2s ease-in-out;
                width: calc(#{$widget-search-form-height} - 5px);
                padding: 0;
                position: absolute;
                right: 5px;
                top: 0;

                &:hover {
                    color: $widget-link-hover-color;
                }

                i {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
    select {
        background-color: transparent;
        border-color: $widget-list-item-border-color;
        border-radius: 0;
        border-style: $widget-list-item-border-style;
        border-width: $widget-list-item-border-width;
        box-sizing: border-box;
        color: $widget-text-color;
        display: inline-block;
        font-family: $default-text-font;
        font-size: 15px;
        font-weight: inherit;
        height: $widget-search-form-height;
        line-height: calc(#{$widget-search-form-height} - 2px);
        margin-top: $widget-list-item-padding;
        outline: 0;
        padding: 0 0 0 21px;
        position: relative;
        transition: color .2s ease-in-out;
        vertical-align: top;
        width: 100%;

        option {
            background-color: $widget-list-item-background-color;
        }
    }

    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        padding-top: $widget-list-item-padding;

        @include mkdfClearfix();

        a {
            background-color: $widget-list-item-background-color;
            border-radius: $default-border-radius;
            color: $widget-text-color;
            display: block;
            float: left;
            font-family: $default-text-font;
            font-size: 15px !important;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0 3px 3px 0;
            padding: 0 26px;

            &:hover {
                background: $widget-text-color;
                color: $widget-list-item-background-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }

        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}