/* ==========================================================================
   Grid System style - begin
   ========================================================================== */

.mkdf-two-columns-form-without-space {
    .mkdf-column-left {
        display: table-cell;
        width: 99%;
        vertical-align: top;
    }

    .mkdf-column-right {
        display: table-cell;
        width: 1%;
        text-align: left;
        vertical-align: top;
    }
}

.mkdf-two-columns-form-with-space {
    .mkdf-column-left {
        display: table-cell;
        width: 99%;
        vertical-align: top;
        padding: 0 10px 0 0;
        box-sizing: border-box;
    }

    .mkdf-column-right {
        display: table-cell;
        width: 1%;
        text-align: left;
        vertical-align: top;
        padding: 0 0 0 10px;
        box-sizing: border-box;
    }
}

/*
* If you changed some value for space do the same thing in product-list-main-and-vc scss files
*/
$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large', 'huge');
$columns_space_width: (0, 5, 10, 15, 20, 25, 40);

@for $i from 0 to length($columns_space_label) {
    .mkdf-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width, $i+1);
        $column_width_margin: $column_width * 2;

        .mkdf-outer-space {
            @if ($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            .mkdf-item-space {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }

        &.mkdf-disable-bottom-space {
            @if ($column_width == 0) {
                margin-bottom: 0;
            } @else {
                margin-bottom: -#{$column_width_margin}px;
            }
        }

        &.mkdf-disable-item-bottom-space {
            .mkdf-item-space {
                margin-bottom: 0;
            }
        }

        &.mkdf-columns-has-side-space {
            padding: 0 #{$column_width_margin}px;
        }
    }
}

$list_columns_map: (
        6 : $laptop-landscape-large-plus-pixel,
        5 : $laptop-landscape-mac-plus-pixel,
        4 : $ipad-landscape-plus-pixel,
        3 : $ipad-portrait-plus-pixel,
        2 : $phone-landscape-plus-pixel
);

$list_columns: ('one', 'two', 'three', 'four', 'five', 'six');

.mkdf-grid-list {
    .mkdf-list-is-slider {
        .mkdf-outer-space {
            margin: 0 !important;
        }

        .mkdf-item-space {
            width: auto !important;
            margin: 0 !important;
            padding: 0 !important;
            float: none !important;
            clear: none !important;
        }
    }

    .mkdf-item-space {
        @include mkdfRelativeHolderLayout(top);

        box-sizing: border-box;
    }

    &:not(.mkdf-one-columns) {
        .mkdf-item-space {
            float: left;
        }
    }

    @for $i from 0 to length($list_columns) {
        $step: $i + 1;

        &.mkdf-#{nth($list_columns, $step)}-columns {
            $column_width: 100%/$step;

            .mkdf-masonry-grid-sizer {
                width: $column_width;
            }

            .mkdf-item-space {
                width: $column_width;

                @if ($i > 0) {

                    &.mkdf-masonry-size-large-width,
                    &.mkdf-masonry-size-large-width-height {
                        width: $column_width*2;
                    }

                    @media only screen and (min-width: map-get($list_columns_map, $step)) {

                        &:nth-child(#{$step}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.mkdf-grid-masonry-list {
    &.mkdf-fixed-masonry-items {
        img {
            height: 100%;
            width: 100%;
        }
    }

    .mkdf-masonry-list-wrapper {
        opacity: 0;
    }

    .mkdf-masonry-grid-sizer {
        width: 100%;
        padding: 0;
        margin: 0;
    }

    .mkdf-masonry-grid-gutter {
        width: 0;
        padding: 0;
        margin: 0;
    }

    .mkdf-item-space {
        &.mkdf-fixed-masonry-item {
            img {
                height: 100%;
                width: 100%;
            }
        }
    }
}

@import 'grid-system-mixin';

.mkdf-grid-row {
    @include createGridRow();
    @include mkdfClearfix();
}

//generate gutter spaces
$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large', 'huge');
$columns_space: (0, 5, 10, 15, 20, 25, 40);

@for $i from 0 to length($columns_space_label) {
    .mkdf-grid-#{nth($columns_space_label,$i+1)}-gutter {
        $item_space: nth($columns_space, $i+1);

        //generate holder margin
        @include createGridRow($item_space);

        //generate columns padding
        > div {
            @include createGridColumnPadding($item_space);
        }
    }
}

//generate column classes
@for $i from 1 to (map-get($gridConfig, numberOfCols) + 1) {
    .mkdf-grid-col-#{$i} {
        @include createGridColumn($i);

        @include createGridColumnResponsive(map-get($gridConfig, stackOn));
    }
}

//generate push / pull classes
@for $i from 1 to (map-get($gridConfig, numberOfCols) + 1) {
    .mkdf-grid-col-push-#{$i} {
        @include createGridColumnPush($i);
    }

    .mkdf-grid-col-pull-#{$i} {
        @include createGridColumnPull($i);
    }
}

//generate responsive width column classes
@for $i from 1 to (map-get($gridConfig, numberOfCols) + 1) {
    @each $size, $width in $breakpoints {
        .mkdf-grid-col-#{$size}-#{$i} {
            $stack: false;

            //check if current column is max number of columns.
            //if it is than we need to stack columns
            @if ($i == map-get($gridConfig, numberOfCols)) {
                $stack: true;
            }

            //if current column isn't the max number of columns
            //than generate responsive for it.
            @if ($i < (map-get($gridConfig, numberOfCols) + 1)) {
                @include createGridColumnResponsive($size, $i, $stack);
            }
        }
    }
}

/* ==========================================================================
   Grid System style - end
   ========================================================================== */