.mkdf-side-menu {
    $source: sidearea;

    // widgets spacing
    $widget-bottom-margin: 42px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 11px;

    $widget-title-margin: 22px;

    // list items color
    $widget-list-item-background-color: $additional-background-color;

    // list items border
    $widget-list-item-border-color: #373737;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #ffffff;
    $widget-heading-color: #ffffff;

    // links
    $widget-link-color: #ffffff;
    $widget-bottom-border: none;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $small-input-height;
    $widget-tag-cloud-height: 43px;

    @import 'widgets/default-wordpress-widgets';
    @import 'widgets/standalone-widgets';

    .widget {
        //    .mkdf-widget-title-holder {
        //        padding-bottom: 0;
        //
        //        .mkdf-widget-title {
        //            font-size: 200px;
        //            margin-bottom: -100px;
        //            opacity: 0.3;
        //        }
        //    }

        &.widget_categories,
        &.widget_product_categories {
            ul,
            li:not(:last-child) {
                &:before {
                    display: none !important;
                }
            }

            ul {
                li {
                    border-bottom-color: $widget-list-item-border-color;
                    border-bottom-style: $widget-list-item-border-style;
                    border-bottom-width: $widget-list-item-border-width;

                    &:last-child {
                        border: 0;
                        padding-bottom: 0;
                    }
                }

                .children,
                .sub-menu {
                    border-top-color: $widget-list-item-border-color;
                    border-top-style: $widget-list-item-border-style;
                    border-top-width: $widget-list-item-border-width;
                }
            }
        }
    }
}