@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();

    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 35px 0;
    }

    .mkdf-comment {
        &.mkdf-pingback-comment {
            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();

            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 23px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 96px;
        height: 96px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 120px;
        min-height: 96px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();

            margin: 5px 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            @include mkdfHeadingStyle(h6);

            position: relative;
            top: 10px;
            text-transform: capitalize;

            &:hover {
                color: $default-heading-color;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            float: left;
            margin: 12px 0 0 0;

            @include mkdfHeadingStyle(h6);
        }

        .mkdf-text-holder p {
            margin: 7px 0;

            @include mkdfHeadingStyle(h6);
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;

                @include mkdfH3();
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 25px;
            font-size: 21px;
            color: $default-text-color;

            &:hover {
                color: $first-main-color;
            }
        }

        ul, ol {
            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 70px 0;
    }

    .comment-reply-title {
        margin: 0 0 33px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait() {
    .mkdf-comment-holder {
        .mkdf-comment-list {
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape() {
    .mkdf-comment-holder {
        .mkdf-comment-list {
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                text-transform: capitalize;
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait() {
    .mkdf-comment-holder {
        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @include mkdfInputStyle(large);
}

#respond input[type='text'] {
    display: inline-block;
    float: left;
    width: calc(33% - 11px);
    //padding: 32px 35px;

    &:not(#url) {
        margin-right: 20px;
    }

    @include ipad-portrait() {
        display: block;
        margin-right: 0;
        width: 100%;
    }
}

#respond textarea {
    height: 230px;
}

.comment-form-cookies-consent {
    clear: both;
    display: block; // MUST BE VISIBLE

    input {
        margin: 3px 15px 3px 0;
    }
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @include mkdfInputStyle(large);

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize(huge);
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

//#submit_comment {
//    @include mkdfButtonSize(huge);
//}

p.form-submit {
    margin: 30px 0 0 0;
}

.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.wpcf7-form .ajax-loader {
    display: none;
}

// woo review form fix
#review_form {
    input[type="text"],
    input[type="email"] {
        @include mkdfInputStyle(large);
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Custom contact form styles - start
   ========================================================================== */

.mkdf-cf-custom {
    input[type='text'],
    input[type='email'],
    textarea {
        margin-bottom: 15px !important;
    }

    input[type='submit'] {
        padding: 24px 25px;
    }

    .mkdf-two-cols {
        input {
            width: calc(50% - 12px);
            display: inline-block;
        }

        span:first-child input {
            margin-right: 24px;
        }

        @include ipad-portrait() {
            input {
                width: 100%;
            }

            span:first-child input {
                margin-right: 0;
            }
        }
    }

    &.mkdf-cf-dark {
        input[type='text'],
        input[type='email'],
        textarea {
            background-color: #000;
            color: #fff !important;
        }
    }
}

