/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */

.mkdf-owl-slider,
.mkdf-owl-custom-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    .owl-stage {
        -webkit-transition-timing-function: $main-easing;
        transition-timing-function: $main-easing;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin

                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {
        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(color .2s ease-out);

            &:hover path {
                fill: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                //font-size: 46px;
                //line-height: 1;
                height: 46px;
                width: 21px;

                svg {
                    width: 21px;
                    top: 50%;
                    position: absolute;

                    @include mkdfTransform(translateY(-50%));
                }

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;

            @include ipad-portrait() {
                left: 10px;
            }

            > span {
                svg {
                    left: 0;
                }
            }
        }

        .owl-next {
            right: 30px;

            @include ipad-portrait() {
                right: 10px;
            }

            > span {
                svg {
                    right: 0;
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 5px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: #c5c5c5;
                box-sizing: border-box;
                border: 1.5px solid transparent;
                background-clip: content-box;

                @include mkdfTransition(.2s);
            }

            &:hover,
            &.active {
                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

