/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */

#mkdf-back-to-top {
    position: fixed;
    width: 60px;
    height: 60px;
    right: 21px;
    bottom: 42px;
    z-index: 10000;
    visibility: hidden;
    -webkit-clip-path: inset(0 0 100% 0);
    clip-path: inset(0 0 100% 0);
    overflow: hidden;

    &.off {
        -webkit-clip-path: inset(0 0 100% 0);
        clip-path: inset(0 0 100% 0);
        visibility: hidden;

        #mkdf-arrow-1 {
            @include mkdfTransform(translateY(180%));
            @include mkdfTransitionTransform(.0s .3s);
        }
    }

    &.on {
        -webkit-clip-path: inset(0 0 0 0);
        clip-path: inset(0 0 0 0);
        visibility: visible;

        @include mkdfTransition(1s $main-easing);

        #mkdf-arrow-1 {
            @include mkdfTransform(translateY(0));
            @include mkdfTransitionTransform(1s $main-easing);
        }
    }

    %pseudo {
        content: '';
        @include mkdfAbsoluteHolderLayout();

        background-color: #fff;
        border-radius: 50%;

        @include mkdfTransition(none);
    }

    > span {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        background-color: #000;
        border-radius: 50%;

        @include mkdfTransition(border-radius 1s $main-easing, background-color 0s 1s);

        &:before {
            @extend %pseudo;

            -webkit-clip-path: inset(100% 0 0 0);
            clip-path: inset(100% 0 0 0);
        }

        &:after {
            @extend %pseudo;

            -webkit-clip-path: inset(0 0 100% 0);
            clip-path: inset(0 0 100% 0);
        }
    }

    svg {
        position: relative;
        z-index: 10;
        mix-blend-mode: difference;
    }

    path {
        fill: #fff;
    }

    #mkdf-arrow-1 {
        @include mkdfTransform(translateY(200%));
    }

    #mkdf-arrow-2 {
        position: absolute;
        top: 10px;
        @include mkdfTransform(translateY(200%));
        @include mkdfTransitionTransform(1s $main-easing);
    }

    &.mkdf-invert {
        > span {
            &:before {
                -webkit-clip-path: inset(0 0 0 0);
                clip-path: inset(0 0 0 0);
                @include mkdfTransition(0s 1s);
                z-index: 5;
            }

            &:after {
                -webkit-clip-path: inset(0 0 0 0);
                clip-path: inset(0 0 0 0);
                @include mkdfTransition(1s $main-easing);
            }
        }
    }

    &:hover {
        > span {
            border-radius: 0;
            @include mkdfTransition(border-radius 1s $main-easing);
        }

        &.mkdf-invert {
            > span {
                background-color: #fff;
            }
        }

        #mkdf-arrow-1 {
            @include mkdfTransform(translateY(-200%));
        }

        #mkdf-arrow-2 {
            top: 16px;
            @include mkdfTransform(translateY(0%));
            @include mkdfTransitionTransform(1s $main-easing .1s);
        }
    }
}

