/* ==========================================================================
   #Row Background styles - begin
   ========================================================================== */

.mkdf-row-has-background-text {
    @include ipad-portrait() {
        .mkdf-row-background-text-holder {
            display: none;
        }
    }

    position: relative;
}

.mkdf-row-background-text-holder {
    @include mkdfAbsoluteHolderLayout();

    .mkdf-row-background-text-wrapper {
        @include mkdfTableLayout();

        .mkdf-row-background-text-wrapper-inner {
            color: #ccc;
            display: table-cell;
            font-size: 302px;
            vertical-align: middle;

            .mkdf-row-background-text-1,
            .mkdf-row-background-text-2 {
                font-weight: 400;
                line-height: 1;
            }
        }

        //@include phone-landscape() {
        //    display: none;
        //}

        &.mkdf-row-background-text-animation {
            visibility: hidden;

            //.touch & {
            //    visibility: visible;
            //}
        }

        .no-touch & {
            &.mkdf-row-background-text-animation {
                .mkdf-char-mask {
                    display: inline-block;
                    overflow: hidden;
                    vertical-align: middle;

                    > span {
                        display: inline-block;

                        @include mkdfTransform(translate3d(-120%, 0, 0));
                        @include mkdfTransitionTransform(2s $main-easing);

                        will-change: transform;
                    }
                }

                &.mkdf-row-background-text-align-right {
                    .mkdf-char-mask > span {
                        @include mkdfTransform(translate3d(120%, 0, 0));
                    }
                }

                .mkdf-char-mask > span.mkdf-show {
                    @include mkdfTransform(translate3d(0, 0, 0));
                }
            }
        }
    }

    @include ipad-landscape() {
        display: none;
    }
}

.touch .mkdf-row-background-text-holder .mkdf-row-background-text-animation {
    visibility: hidden;
}

.touch .mkdf-row-has-background-text .mkdf-row-background-text-holder .mkdf-row-background-text-animation {
    visibility: visible;
}

