// svg opener common styles

a {
    &[class*=-svg-path] {
        cursor: pointer;
        height: 65px;
        width: 65px;

        > span {
            display: flex !important;
            height: 65px;
            justify-content: center;
            left: 50% !important;
            position: absolute !important;
            top: 50% !important;
            width: 65px;

            @include mkdfTransform(translate(-50%, -50%));

            svg {
                align-self: center;

                * {
                    fill: none;
                    stroke: none;
                }

                circle {
                    fill: #000000;
                }

                rect {
                    fill: #ffffff;
                }
            }
        }

        circle {
            @include mkdfTransformOrigin(50% 50%);
            @include mkdfTransitionTransform(2s cubic-bezier(0.07, 0.98, 0, 0.96));
        }

        rect {
            &:nth-child(2) {
                @include mkdfTransformOrigin(0% 33%);
                @include mkdfTransitionTransform(.15s .1s);
            }

            &:nth-child(3) {
                @include mkdfTransformOrigin(0% 70%);
                @include mkdfTransitionTransform(.15s);
            }

            &:nth-child(4) {
                @include mkdfTransformOrigin(0% 35%);
                @include mkdfTransitionTransform(.15s .05s);
            }

            &:nth-child(5) {
                @include mkdfTransformOrigin(0% 68%);
                @include mkdfTransitionTransform(.15s .15s);
            }
        }

        @include keyframes(mkdfSVGLine) {
            0% {
                @include mkdfTransform(scaleY(1));
                @include mkdfTransformOrigin(50% 33%);

            }

            34% {
                @include mkdfTransform(scaleY(0));
                @include mkdfTransformOrigin(50% 33%);
            }

            35% {
                @include mkdfTransform(scaleY(0));
            }

            36% {
                @include mkdfTransform(scaleY(0));
                @include mkdfTransformOrigin(50% 66%);
            }

            100% {
                @include mkdfTransform(scaleY(1));
                @include mkdfTransformOrigin(50% 66%);
            }
        }

        body[class*="opened"] &,
        &:not(.mkdf-mobile-menu-opener-svg-path):hover {
            circle {
                @include mkdfTransform(scale(1.6));
            }

            rect:nth-child(3),
            rect:nth-child(4) {
                @include mkdfTransform(scaleY(0));
            }

            rect:nth-child(2) {
                @include mkdfTransform(scaleY(1.51));
            }

            rect:nth-child(5) {
                @include mkdfTransform(scaleY(2));
            }

            .mkdf-fullscreen-menu-opener-icon rect:last-child {
                @include mkdfAnimation(mkdfSVGLine .7s $main-easing);
            }
        }
    }

    // sidearea close
    &.mkdf-close-side-menu {
        .mkdf-header-icon-label {
            color: #ffffff !important;
        }

        > span {
            svg {
                rect {
                    fill: #ffffff;
                }
            }
        }
    }

    // fullscreen close
    &.mkdf-fullscreen-menu-opener {
        .mkdf-fullscreen-menu-close-icon {
            .mkdf-header-icon-label {
                color: #ffffff;
            }

            svg {
                rect {
                    fill: #ffffff;
                }
            }
        }
    }

    @at-root .mkdf-mobile-header {
        a {
            &.mkdf-fullscreen-menu-opener {
                .mkdf-fullscreen-menu-close-icon {
                    .mkdf-header-icon-label {
                        color: #000000;
                    }

                    svg {
                        rect {
                            fill: #000000;
                        }
                    }
                }
            }
        }
    }

    // svg openers w/ label
    &.mkdf-expanding-menu-opener,
    &.mkdf-fullscreen-menu-opener,
    &.mkdf-mobile-menu-opener {
        margin-left: $opener-margin;
    }

    @include laptop-landscape-medium {
        &.mkdf-expanding-menu-opener {
            margin-left: 15px;

            .mkdf-header-icon-label {
                display: none;
            }
        }
    }
}

// regular openers w/ label
.mkdf-shopping-cart-holder,
.mkdf-search-opener {
    margin-left: $opener-margin;
}

