footer {
    $source: footer;

    // widgets spacing
    $widget-bottom-margin: 45px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 7px;

    $widget-title-margin: 25px;

    // list items color
    $widget-list-item-background-color: #444444;

    // list items border
    $widget-list-item-border-color: #ffffff;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: dotted;

    // text
    $widget-text-color: #ffffff;
    $widget-heading-color: #ffffff;

    // links
    $widget-link-color: #ffffff;
    $widget-bottom-border: none;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $small-input-height;
    $widget-tag-cloud-height: 43px;

    font-size: 16px;
    line-height: 1.3em;

    @import 'widgets/default-wordpress-widgets';
    @import 'widgets/standalone-widgets';

    .mkdf-footer-bottom-holder {
        .widget {
            margin: 0;

            &.widget_text p:last-child {
                padding-top: 0;
            }
        }
    }
}
