header {

    // only vars for social share widget

    $source: header;
    $widget-text-color: inherit;
    $widget-heading-color: inherit;
    $widget-link-color: inherit;
    $widget-link-hover-color: $first-main-color;
    $widget-list-item-padding: 0;

    @import 'widgets/standalone-widgets';

    .mkdf-expanding-menu-opened & {
        $source: header;
        $widget-text-color: #ffffff;
        $widget-heading-color: #ffffff;
        $widget-link-color: #ffffff;
        $widget-link-hover-color: $first-main-color;

        @import 'widgets/standalone-widgets';
    }

    .mkdf-light-header:not(.mkdf-expanding-menu-opened) & {
        $source: header;
        $widget-text-color: $header-light-color;
        $widget-heading-color: $header-light-color;
        $widget-link-color: $header-light-color;
        $widget-link-hover-color: $header-light-hover-color;

        @import 'widgets/standalone-widgets';
    }

    .mkdf-light-header.mkdf-expanding-menu-opened & {
        $source: header;
        $widget-text-color: $header-dark-color;
        $widget-heading-color: $header-dark-color;
        $widget-link-color: $header-dark-color;
        $widget-link-hover-color: $header-dark-hover-color;

        @import 'widgets/standalone-widgets';
    }

    .mkdf-dark-header:not(.mkdf-expanding-menu-opened) & {
        $source: header;
        $widget-text-color: $header-dark-color;
        $widget-heading-color: $header-dark-color;
        $widget-link-color: $header-dark-color;
        $widget-link-hover-color: $header-dark-hover-color;

        @import 'widgets/standalone-widgets';
    }

    .mkdf-dark-header.mkdf-expanding-menu-opened & {
        $source: header;
        $widget-text-color: $header-light-color;
        $widget-heading-color: $header-light-color;
        $widget-link-color: $header-light-color;
        $widget-link-hover-color: $header-light-hover-color;

        @import 'widgets/standalone-widgets';
    }
}